import { FC, useCallback } from "react";
import { read_cookie, delete_cookie } from "sfcookies";

import { ViewType } from "../../../types";
import { logout, useMainContext } from "../../../utils";
import { Button } from "../../atoms/button";

interface Props {
  onViewChange: (viewType: ViewType) => void;
}
export const TopBar: FC<Props> = ({ onViewChange }) => {
  const { checkSession, isTestMode, setIsTestMode } = useMainContext();

  const onLogoutClick = useCallback(() => {
    const cookieId = read_cookie("fishki");
    logout(cookieId).then((res) => {
      if (res.cookieDeleted) {
        delete_cookie("fishki");
      }
      checkSession();
    });
  }, [checkSession]);

  const toggleTestMode = useCallback(
    (_e) => {
      const testMode = isTestMode;
      setIsTestMode(!testMode);
    },
    [isTestMode, setIsTestMode]
  );

  return (
    <>
      <Button onClick={onLogoutClick} className="test-button">
        Logout
      </Button>

      <Button onClick={toggleTestMode} className="test-button">
        Test Mode
      </Button>

      <Button
        onClick={() => onViewChange(ViewType.LESSON)}
        className="start-lesson-button"
      >
        Start Lesson
      </Button>
      <Button
        onClick={() => onViewChange(ViewType.SHOW_ALL_CARDS)}
        className="show-cards-button"
      >
        Show All Cards
      </Button>
      <Button
        onClick={() => onViewChange(ViewType.CREATE_NEW_CARD)}
        className="create-card-button"
      >
        Create New Card
      </Button>
      <Button
        onClick={() => onViewChange(ViewType.CREATE_NEW_COLLECTION)}
        className="create-collection-button"
      >
        Create New Collection
      </Button>
      <Button
        onClick={() => onViewChange(ViewType.SHOW_STATISTICS)}
        className="show-statistics-button"
      >
        Show Statistics
      </Button>
    </>
  );
};
