import { FC } from "react";
import styled from "styled-components";
import { Card, ViewType } from "../../../types";
import { useMainContext } from "../../../utils";
import { CardComponent } from "../../organisms/card";
import { CreateCard } from "../../organisms/create-card";
import { CreateCollection } from "../../organisms/create-collection";

import { Lesson } from "../../organisms/lesson";
import { Statistics } from "../../organisms/statistics";

interface Props {
  viewType: ViewType;
}
export const MainView: FC<Props> = ({ viewType }) => {
  const { cards } = useMainContext();

  if (viewType === ViewType.MAIN) {
    return <div>main</div>;
  } else if (viewType === ViewType.LESSON) {
    return <Lesson />;
  } else if (viewType === ViewType.SHOW_ALL_CARDS) {
    return (
      <CardsWrapper>
        {cards &&
          cards.map((card: Card) => {
            return <CardComponent card={card} key={card._id} />;
          })}
      </CardsWrapper>
    );
  } else if (viewType === ViewType.CREATE_NEW_CARD) {
    return <CreateCard />;
  } else if (viewType === ViewType.SHOW_STATISTICS) {
    return <Statistics />;
  } else if (viewType === ViewType.CREATE_NEW_COLLECTION) {
    return <CreateCollection />;
  } else {
    return <div>else</div>;
  }
};

const CardsWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
`;
