import { FC, useCallback, useEffect, useState } from "react";
import styled from "styled-components/macro";

import { ViewType } from "../../../types";
import { LoginStatus } from "../../molecules/login-status";
import { LoginForm } from "../../molecules/login-form";
import { CheckingSession } from "../checking-session";
import { useMainContext } from "../../../utils";

import { MainView } from "../../views/main-view";
import { TopBar } from "../../views/top-bar";

import "../../../theme/variables.scss";

interface MainProps {}

export const Main: FC<MainProps> = () => {
  const [loading, setLoading] = useState(true);
  const {
    fetchCards,
    isLoggedIn,
    checkSession,
    isSessionChecking,
  } = useMainContext();
  const [viewType, setViewType] = useState(ViewType.MAIN);

  useEffect(() => {
    checkSession();
    fetchCards();
  }, [checkSession, fetchCards]);

  useEffect(() => {
    if (isSessionChecking) {
      setLoading(true);
    }
    if (!isSessionChecking) {
      setTimeout(() => {
        setLoading(false);
      }, 500);
    }
  }, [isSessionChecking]);

  const onViewChange = useCallback((viewType) => {
    setViewType(viewType);
  }, []);

  return (
    <MainContainer>
      {loading && <CheckingSession />}
      <LoginStatus />

      {isLoggedIn && (
        <>
          <TopBar onViewChange={onViewChange} />
          <MainView viewType={viewType} />
        </>
      )}

      {!isLoggedIn && <LoginForm />}
    </MainContainer>
  );
};

const MainContainer = styled.div`
  display: flex;
  flex-direction: column;
  background-color: var(--tundora);
  background: linear-gradient(#d9d8df, #a19eae);

  .test-button {
    max-width: 100px;
  }
  .start-lesson-button {
    max-width: 200px;
  }
  .show-cards-button {
    max-width: 200px;
  }

  .create-card-button {
    max-width: 200px;
  }
  .create-collection-button {
    max-width: 200px;
  }
  .show-statistics-button {
    max-width: 200px;
  }
`;
